* {
    padding: 0;
    margin: 0;
}

body {
    background-color: rgb(250, 250, 250);
}

.md-toolbar md-icon.md-default-theme {
    color: white;
}

.md-toolbar-tools {
    color: white;
}

.md-input-container.md-default-theme label,
.md-input-container.md-default-theme .md-placeholder {
    color: white !important;
}

.nav, .pagination, .carousel, .panel-title a {
    cursor: pointer;
}

.reduce-margin {
    margin: 5px 0;
}

#dropDown {
    display: none;
    background-color: white;
    color: black;
    border-radius: 4px;
    position: absolute;
    top: 54px;
    right: 5px
}

.drop-down-profile-triangle {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 15px 15px 15px;
    border-color: transparent transparent #ffffff transparent;
    position: absolute;
    top: -10px;
    right: 10px
}

.change-profile-image-text {
    width: 100%;
    height: 25px;
    position: absolute;
    bottom: 0;
    background-color: #000000;
    color: white;
    text-align: center;
    font-size: 12px;
    opacity: 0.7
}

.fixed-calendar {
    width: 340px;
    height: 340px;
    display: block;
}

.fixed-calendar .md-calendar-scroll-mask {
    width: 340px !important;
}

.md-calendar-day-header {
    width: 340px;
}

.fixed-calendar .md-virtual-repeat-scroller {
    width: 340px !important;
    height: 308px;
}

.schedule-day-label {
    width: 100%;
    padding: 5px;
    background-color: #2E4454;
    color: #fff;
    text-align: center
}

.schedule-item {
    width: 90%;
    background: #de5a8c;
    color: white;
    padding: 2px;
    text-align: center;
    border-radius: 4px;
    margin: 2px auto;
}

.schedule-agenda {
    background: #de5a8c;
    color: white;
    padding: 2px 10px;
    text-align: center;
    border-radius: 4px;
    margin: 2px;
}

.wait {
    opacity: 0.4;
}

.schedule-item span {
    display: block;
}

.schedule-item h5 {
    margin: 5px auto;
}

.extra {
    background: #3d5972;
}

.unread {
    background: lightgray;
}

.notification-circle {
    width: 16px;
    height: 16px;
    font-size: 12px;
    background: #FF5350;
    border-radius: 100%;
    color: white;
    text-align: center;
}

.personal-info-text {
    font-size: 18px;
}

md-progress-circular svg {
    left: 75px;
}

md-input-container:not(.md-input-has-value) input:not(:focus) {
    color: black;
}

.no-outline:focus {
    outline: none;
}

.small-progress svg {
    left: 0;
    width: 30px !important;
    height: 30px !important;
    margin: 10px;
}